import React,{useRef,useState} from 'react';
import { Form,Button,Card,Alert } from 'react-bootstrap';
import { useAuth } from '../contexts/AuthContext';
import  { Link,useHistory } from 'react-router-dom';

function UpdateProfile(){
 const emailRef=useRef();
 const passwordRef=useRef()
 const confirmPasswordRef=useRef();
 const { user,updateEmail,updatePassword } =useAuth();
 const [error,setError] =useState('');
 const [loading ,setLoading]=useState(false);
 const history = useHistory();

 function handleSubmit(e){
      e.preventDefault();
      if(passwordRef.current.value !== confirmPasswordRef.current.value)
      {
        return setError("password do not match");
      }
      const promises=[];

      if(emailRef.current.value!=user.email)
          promises.push(updateEmail(emailRef.current.value));
      if(passwordRef.current.value && confirmPasswordRef.current.value)
          promises.push(updatePassword(passwordRef.current.value));

      Promise.all(promises).then(()=>{
        history.push("/");
        }).catch(()=>{
          setError("Failed to update the account");
        }).finally(()=>{
        setLoading(false);
        })
}
    return (
        <React.Fragment>
        <Card>
        <Card.Body>
        <h2 className="text-center mb-4">Update Profile</h2>
        {error && <Alert variant="danger">{error}</Alert>}
        <Form onSubmit={handleSubmit}>
          <Form.Group id="email">
             <Form.Label>Email</Form.Label>
             <Form.Control type="email" ref={emailRef} required defaultValue={ user && user.email}/>
          </Form.Group>
          <Form.Group id="password">
             <Form.Label>Password</Form.Label>
             <Form.Control type="password" ref={passwordRef}  placeholder="Leave blank to keep the same"/>
          </Form.Group>
          <Form.Group id="confirm_password">
             <Form.Label>Confirm Password</Form.Label>
             <Form.Control type="password" ref={confirmPasswordRef}  placeholder="Leave blank to keep the same"/>
          </Form.Group>
          <Button disabled= {loading} className="w-100" type="submit">
          Update Profile
          </Button>
        </Form>
        </Card.Body>
        </Card>
        <div className="w-100 text-center mt-2">
          <Link to="/">Cancel</Link>
        </div>
        </React.Fragment>
    );
}

export default UpdateProfile;